.list-filter {
  display: flex;
  flex-flow: row nowrap; }

.list-filter__title {
  font-weight: 200;
  color: darkgrey; }

.list-filter__item, .list-filter__item__container__input {
  display: block; }

.list-filter__item {
  flex-basis: 25%; }
  .list-filter__item > .simple-select {
    width: 100%; }

.list-filter__item__input .DateInput {
  width: 40%; }
  .list-filter__item__input .DateInput > input {
    width: 100%; }

.list-filter__item__container {
  display: flex;
  flex-flow: column; }
  .list-filter__item__container .form__label--small {
    display: flex; }

.list-filter__item__container__input {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  align-items: center; }

.list-filter__item__container__input__appendage {
  width: 1.2rem;
  height: 1.2rem;
  position: absolute;
  right: 2.5rem;
  top: 50%;
  transform: translateY(-50%);
  line-height: 1; }

.list-filter__item__container__input__suffix {
  width: 30px;
  height: 30px;
  display: block;
  display: flex;
  align-items: center;
  justify-content: center; }

.list-filter__item--predefined {
  display: flex;
  flex-direction: column;
  position: relative;
  align-self: flex-end; }
  .list-filter__item--predefined.is-open .list-filter__item__filter-collapsable {
    max-height: 50rem;
    opacity: 1;
    overflow: visible; }

.list-filter__item__filter-btn-container {
  width: 100%; }

.list-filter__item__filter-btn {
  padding: 1em;
  min-width: auto;
  width: 1em; }

.list-filter__item__filter-collapsable {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  top: 100%;
  z-index: 9;
  background-color: white;
  box-shadow: 0px 0px 10px lightgrey;
  transition: all 350ms ease-in-out; }
  .list-filter__item__filter-collapsable:before {
    content: '';
    border-bottom: 10px solid white;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    height: 0;
    left: 7%;
    position: absolute;
    top: -10px;
    transform: translateX(-50%);
    width: 0; }
