.drop-options-container {
  display: flex;
  flex-flow: row nowrap;
  height: 100%;
  justify-content: flex-end;
  position: relative;
  text-align: right;
  min-width: 15rem; }

.drop-options {
  position: relative;
  text-align: right;
  height: 100%;
  flex-basis: 15rem; }

.drop-options__base {
  display: flex;
  flex-flow: column nowrap;
  justify-content: stretch;
  height: 100%; }

.drop-options__base__btn {
  display: block;
  background-color: white;
  width: 100%;
  height: 100%; }
  .drop-options__base__btn:hover {
    background-color: lightgray; }

.drop-options__options {
  transition: all 350ms ease-in-out;
  color: black;
  position: absolute;
  top: 105%;
  right: 0;
  width: 100%;
  z-index: 999;
  box-shadow: 0px 0px 10px black;
  background-color: lightgray;
  transition: opacity 350ms ease-in-out, max-height 350ms ease-in-out;
  border-width: 0;
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding-bottom: 0;
  padding-top: 0; }
  .drop-options__options:before {
    content: '';
    border-bottom: 10px solid grey;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    height: 0;
    left: 1.5rem;
    position: absolute;
    top: -10px;
    transform: translateX(-50%);
    width: 0; }
  .drop-options__options.drop-options__options--is-open {
    opacity: 1;
    max-height: 20rem;
    overflow: visible; }

.drop-options__options__item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: stretch;
  align-items: center;
  border-bottom: 1px solid black;
  min-height: 3rem; }
  .drop-options__options__item:last-child {
    border-bottom: none; }
  .drop-options__options__item > .drop-options__options__item__default {
    width: 100%;
    height: 100%;
    text-align: right;
    color: black;
    background-color: transparent;
    cursor: pointer; }
    .drop-options__options__item > .drop-options__options__item__default:hover {
      background-color: lightgray; }
